  function priorityFn(name: string) {
    const rank = name.includes("")
      ? 1
      : name.includes("")
      ? 2
      : name.includes("RSG")
      ? 2
      : name.includes("")
      ? 3
      : name.includes("")
      ? 3
      : name.includes("")
      ? 4
      : name.includes("ZG")
      ? 5
      : name.includes("")
      ? 5
      : name.includes("")
      ? 5
      : name.includes("")
      ? 6
      : name.includes("GR")
      ? 6
      : 0;
    return rank;
  }
  const SortApiBalance = computed(() => {
    const sort = Object.keys(ApiBalance.value)
      .map((key) => [key, ApiBalance.value[key]])
      .sort((a, b) => {
        console.log(a[0], b[0]);
        return priorityFn(a[0].toString()) - priorityFn(b[0].toString());
      });
    return sort;
  });
